import { useState } from 'react'
import { NavLink } from 'react-router-dom'

export const AppHeader = () => {

    const [navbar, setNavbar] = useState(false)
    const changeBackground = () =>
        window.scrollY >= 75 ? setNavbar(true) : setNavbar(false)
    window.addEventListener('scroll', changeBackground)

    return <header className={navbar ? "app-header active" : "app-header"}>
        <section className="main-layout flex j-between a-center">
            <NavLink to="/" className="logo flex a-center" onClick={() => window.location.reload()}>
                <img src={`${process.env.PUBLIC_URL}/favicon.png`} alt=""/>
                <span>LOUDER</span>
            </NavLink>
            <nav className="flex a-center">
                <a href="https://github.com/tomleb3/louder">
                    <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round"
                        strokeLinejoin="round" className="github-link" height="1.5em" width="1.5em">
                        <path d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35
                         6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65
                          16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44
                           5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"></path>
                    </svg>
                </a>
            </nav>
        </section>
    </header>
}